<template>
  <div class="tv" >
    <Characters ref="chars" class="tv__chars"/>
    <div class="tv__lines"></div>
    <div class="tv__btns">
      <div class="tv__btns--bt" :style="{'background-image':'url('+this.images.btnDiscord+')'}"></div>
      <div class="tv__btns--bt" :style="{'background-image':'url('+this.images.btnFace+')'}"></div>
      <div class="tv__btns--bt" :style="{'background-image':'url('+this.images.btnTwitter+')'}"></div>
    </div>
  </div>
</template>


<script>
import Characters from './Characters.vue'
export default {
  name: 'TvScreen',
  components: {
    Characters,
  },
  data() {
    return {
      animaaiclass: 'animaii',
      animaai: '',
      animeDelay: 1500,
      totalHeight: 0,
      images: {
        btnDiscord: require('@/assets/btnDiscord.png'),
        btnFace: require('@/assets/btnFace.png'),
        btnTwitter: require('@/assets/btnTwitter.png'),
      }
    }
  },
  created() {
    this.addAnimaii()
  },
  methods: {
    addAnimaii() {
      const rananim =  this.animeDelay * (Math.floor(Math.random() * 10) + 2);
      setTimeout(() => {
        this.animaai = this.animaaiclass;
        this.$refs.chars.addAnimaii();
        this.removeAnimaii();
      }, rananim);
    },

    removeAnimaii() {
      setTimeout(() => {
        this.animaai = '';
        this.$refs.chars.removeAnimaii();
        this.addAnimaii();
      }, 2000);
    }
  }
}
</script>
