      &:hover {
        opacity: 1;
        filter: drop-shadow( -1px 10px 10px rgba(white, .1));
      }
      &:active {
        filter: invert(70%);
      }
    }
  }
}
@keyframes turnOn {
  0% {
    filter: invert(100%); opacity: 0;
  }
  50% {
